"use client";
import { FaArrowUp } from "react-icons/fa";
import { motion } from "framer-motion";
import { useEffect, useState } from "react";

export default function Footer() {
  const [showButton, setShowButton] = useState(false);

  useEffect(() => {
    const handleScroll = () => setShowButton(window.scrollY > 200);
    window.addEventListener("scroll", handleScroll);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const scrollTop = () => window.scrollTo({ top: 0, behavior: "smooth" });

  return (
    <>
      {/* ðŸ¦¶ FOOTER FIJO ABAJO */}
      <footer className="w-full py-6 text-center text-gray-400 text-sm bg-transparent z-30">
        Â© {new Date().getFullYear()} Patricio Mardones
      </footer>

      {/* ðŸ”¼ BOTÃ“N SUBIR (flotante) */}
      {showButton && (
        <motion.button
          onClick={scrollTop}
          initial={{ opacity: 0, y: 40 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.4 }}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
          className="fixed bottom-6 right-6 flex items-center gap-2 px-4 py-2 
          bg-gradient-to-tr from-blue-500 to-purple-600 text-white 
          rounded-full shadow-lg hover:shadow-blue-500/30 transition-transform z-50"
        >
          <FaArrowUp /> Subir
        </motion.button>
      )}
    </>
  );
}
